import React from "react";
import axios from "axios";
import "./addcostumer.css";

import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";

import { Link } from "react-router-dom";

const authKey = sessionStorage.getItem("access_token");
const createCostumer_server = `createcostumer/${authKey}/`;

export default class addCostumer extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			name: "",
			loanammount: 0,
			loan_date: null,
			loanInterst: 0,
			ms: 0,
			place: "",
			ammount_Balance: 0,
			place_id: 0,
			showAlert: false,
		};
	}

	showToast = () => {
		if (this.state.showAlert === false) {
			this.setState({
				showAlert: true,
			});
		} else {
			this.setState({ showAlert: false });
		}
	};

	setStateValue = () => {
		let CostumerInput = {
			name: this.state.name,
			loanammount: this.state.loanammount,
			loan_date: this.state.loan_date,
			loanInterst: this.state.loanInterst,
			ms: this.state.ms,
			place: this.state.place,
			ammount_Balance: this.state.ammount_Balance,
			place_id: this.state.place_id,
		};
		this.savecostumerData(CostumerInput);
	};

	savecostumerData = async (UserData) => {
		axios.post(createCostumer_server, UserData).then(
			(result) => {
				this.setState({
					showAlert: true,
				});
			},
			(error) => {
				console.log(error, "error");
			}
		);
	};

	render() {
		return (
			<div className="w-100 d-flex align-middle justify-content-center">
				{this.state.showAlert && (
					<div className="h-auto top-0 end-0 rounded d-flex justify-content-center align-middle p-3 flex-row gap-4 position-fixed bg-warning">
						<p cliassName="badge text-dark bg-warning p-3">
							Costumer Added Successfully..
						</p>
						<button onClick={this.showToast} className="btn">
							❌
						</button>
					</div>
				)}
				<div className="shadow-sm d-flex w-25 p-3 flex-column gap-2">
					<input
						type="text"
						onChange={(e) => {
							this.setState({ name: e.target.value });
						}}
						placeholder="Costumer Name"
						className="form-control"
						name="name"
					/>
					<input
						type="number"
						onChange={(e) => {
							this.setState({
								loanammount: e.target.value,
								ammount_Balance: e.target.value,
								loanInterst: e.target.value / 10,
							});
						}}
						placeholder="Loan Ammount"
						className="form-control"
						name="loanammount"
					/>
					<input
						type="number"
						onChange={(e) => {
							this.setState({ ammount_Balance: e.target.value });
						}}
						value={this.state.ammount_Balance}
						placeholder="Ammount Balance"
						className="form-control"
						name="ammount_Balance"
					/>
					<input
						type="date"
						className="form-control"
						onChange={(e) => {
							this.setState({ loan_date: e.target.value });
						}}
					/>
					<input
						type="text"
						onChange={(e) => {
							this.setState({ loanInterst: e.target.value });
						}}
						value={this.state.loanInterst}
						placeholder="Loan Intrest"
						className="form-control"
						name="loanInterst"
					/>
					<input
						type="text"
						onChange={(e) => {
							this.setState({ ms: e.target.value });
						}}
						placeholder="Loan MS"
						className="form-control"
						name="ms"
					/>
					<input
						type="text"
						onChange={(e) => {
							this.setState({ place: e.target.value });
						}}
						placeholder="Place"
						className="form-control"
						name="place"
					/>
					<input
						type="text"
						onChange={(e) => {
							this.setState({ place_id: e.target.value });
						}}
						className="form-control"
						placeholder="Place Id"
						name="place_id"
					/>
					<button
						className="btn btn-primary"
						onClick={this.setStateValue}
					>
						Save Costumer Data
					</button>
					<Link className="btn btn-primary" to="/updateCostumer">
						Update Costumers
					</Link>
				</div>
			</div>
		);
	}
}
