import React, { useState, useEffect } from "react";
import "./viewCostumers.css";
import axios from "axios";

const costumerServer = `getcostumer/`;
const authKey = sessionStorage.getItem("access_token");

export default function ViewCostumers() {
	const [costumerDetails, setCostumerDetails] = useState([
		{
			name: "",
			loanammount: "",
			loan_date: "",
			loanInterst: "",
			ms: "",
			place: "",
			ammount_Balance: "",
			place_id: "",
		},
	]);
	const [searchTerm, setSearchTerm] = useState("");
	const [cashinHand, setCashinHand] = useState("");
	const [ammountBalance, setAmmountBalance] = useState("");
	const [serverurl, setserverurl] = useState("");

	const calculateCashinHand = () => {
		setCashinHand(localStorage.getItem("aih"));
	};

	const calculateCashinline = async () => {
		let ab = 0;
		await axios.get(`${costumerServer}${authKey}/`).then((res) => {
			res.data.map((data) => {
				return (ab = parseInt(data.ammount_Balance) + parseInt(ab));
			});
		});
		setAmmountBalance(ab);
	};

	const searchcostumer = (e) => {
		setSearchTerm(e.target.value);
	};

	const getCostumerData = () => {
		axios.get(`${costumerServer}${authKey}/`).then(
			(result) => {
				setCostumerDetails(result.data);
			},
			(error) => {
				alert(
					"Please Start the Server {to start server ==> open Server.bat File}📂📂🔥🔥"
				);
			}
		);
	};

	const uploadData = () => {
		let create;
		axios.get(serverurl).then((res) => {
			res.data.map((data) => {
				create = {
					name: data.name,
					loanammount: data.loanammount,
					loan_date: data.loan_date,
					loanInterst: data.loanInterst,
					ms: data.ms,
					place: data.place,
					ammount_Balance: data.ammount_Balance,
					place_id: data.place_id,
				};
				axios.post(
					`http://localhost:8000/createcostumer/${authKey}/`,
					create
				);
				return "function exit";
			});
		});
	};

	useEffect(() => {
		calculateCashinHand();
		getCostumerData();
		calculateCashinline();
	}, [1]);

	var date;

	function addDays(date) {
		let now = new Date();
		const copy = new Date(Number(date));
		copy.setDate(date.getDate() + 100);
		var copyDate = new Date(copy);
		if (now > copyDate) {
			return true;
		}
		return false;
	}

	const csid = async (id) => {
		await sessionStorage.setItem("getcollection", id);
		window.location.href = "/costumercollection";
	};

	const searchdata = costumerDetails
		.filter((details) => {
			return details.name
				.toLowerCase()
				.includes(searchTerm.toLowerCase());
		})
		.map((details, index) => {
			date = new Date(details.loan_date);
			date = addDays(date);
			return (
				<div
					onClick={() => csid(details.id)}
					className="viewcostumers_tb_tb"
					key={index}
				>
					<p className={date === false ? "normal" : "late"}>
						{details.name}
					</p>
					<h6>Place: &nbsp;{details.place}</h6>
					<h6>Loan Ammount: &nbsp;{details.loanammount}</h6>
					<h6>Balance:&nbsp;{details.ammount_Balance}</h6>
					<h6>Loan Date: &nbsp;{details.loan_date}</h6>
				</div>
			);
		});

	return (
		<>
			<div className="d-flex flex-row justify-content-between">
				<div className="d-flex flex-column text-primary w-50">
					<h3>Cash in Hand: {cashinHand} &nbsp;💰💰</h3>
					<h3>Cash in Line: {ammountBalance} 💰💰 </h3>
				</div>
				<input
					type="text"
					id="outlined-search"
					onChange={searchcostumer}
					className="form-control w-25 h-25"
					placeholder="Search Value"
				/>
			</div>

			<div className="viewcostumers_table">
				<div className="viewcostumers_tb">{searchdata}</div>
			</div>
			<div className="d-flex flex-row gap-3 p-3">
				<input
					type="url"
					className="form-control h-25"
					placeholder="URL"
					onChange={(e) => setserverurl(e.target.value)}
				/>
				<button className="btn btn-success h-25 w-25" onClick={uploadData}>
					Upload Data from Json Server
				</button>
			</div>
		</>
	);
}
