import React, { Component } from "react";
import axios from "axios";

import "./planclac.css";

const authKey = sessionStorage.getItem("access_token");

const getcostumerDataServer = `getcostumer/${authKey}`;
const otherAmmountOutServer = `getoao`;
const deleteCostumerserver = `deletecostumer/`;
const ammountinhandServer = `getaih`;
const postammountinhandServer = `createaih`;
const inversmentServer = `getinversment`;
const otherammuntINServer = `getoai`;
const othersServer = `getothers`;
const closeupserver = `getcloseup`;
const closeDownserver = `getclosedown`;

class Plancalc extends Component {
	constructor(props) {
		super(props);
		this.state = {
			date: "",
			cashInHand: "",
			Inversemet: 0,
			other_ammount_in: 0,
			closeUp: 0,
			dl: 0,
			expence: 0,
			loan: 0,
			intrest: 0,
			ms: 0,
			other_ammount_out: 0,
			closeDown: 0,
			Others: 0,
			AmmountAdd: 0,
			AmmountSub: 0,
			cashINHand1: 0,
		};
	}

	getAmmounts = async () => {
		await this.calculateCashinHand(); //irruppu
		await this.calculateInversment(); // muthaleedu
		await this.other_ammount_in(); // line athigam
		await this.closeUp(); // attai athigam
		await this.closeDown(); // attai kuaivu
		await this.other_ammount_out(); // line kuraivu
		await this.Others(); //kavar edduppu and sambalam
		await this.setState({
			dl: localStorage.getItem("dl"),
			expence: localStorage.getItem("expence"),
			loan: localStorage.getItem("loan"),
			intrest: localStorage.getItem("interst"),
			ms: localStorage.getItem("ms"),
			date: localStorage.getItem("date"),
		});
		await this.ammountsTobe__added(); // calculation of ammount to bs added
		await this.ammountsTobe__Sub(); // calculation of ammount to bs sub
		await this.calculateAmmountInHand();
		await localStorage.setItem("aih", this.state.cashINHand);
	};

	componentDidMount() {
		this.getAmmounts();
	}

	calculateAmmountInHand = async () => {
		const { AmmountAdd, AmmountSub } = this.state;
		let cashINHand1 = 0;
		cashINHand1 = parseInt(AmmountAdd) - parseInt(AmmountSub);
		await this.setState({
			cashINHand1,
		});
	};

	ammountsTobe__added = async () => {
		let {
			cashInHand,
			Inversemet,
			other_ammount_in,
			dl,
			closeUp,
			intrest,
			ms,
		} = this.state;
		let AmmountAdd = 0;
		AmmountAdd =
			parseInt(cashInHand) +
			parseInt(Inversemet) +
			parseInt(other_ammount_in) +
			parseInt(dl) +
			parseInt(closeUp) +
			parseInt(intrest) +
			parseInt(ms);
		await this.setState({
			AmmountAdd,
		});
	};

	ammountsTobe__Sub = async () => {
		let { expence, other_ammount_out, Others, loan, closeDown } =
			this.state;
		let AmmountSub = 0;
		AmmountSub =
			parseInt(expence) +
			parseInt(other_ammount_out) +
			parseInt(Others) +
			parseInt(loan) +
			parseInt(closeDown);
		await this.setState({
			AmmountSub,
		});
	};

	other_ammount_out = async () => {
		let other_ammount_out = 0;
		await axios.get(`${otherAmmountOutServer}`).then((res) => {
			let other_ammount_out1 = res.data.filter((other_ammount_out) =>
				other_ammount_out.date.includes(localStorage.getItem("date"))
			);
			other_ammount_out1.map((data) => {
				return (other_ammount_out =
					parseInt(data.ammount) + parseInt(other_ammount_out));
			});
		});
		await this.setState({
			other_ammount_out,
		});
	};

	getCostumerData = async () => {
		let CostumerData;
		await axios.get(`${getcostumerDataServer}`).then((res) => {
			CostumerData = res.data;
		});
		CostumerData.map((data) => {
			if (data.ammount_Balance === "0") {
				return axios
					.delete(`${deleteCostumerserver}${data.id}/${authKey}/`)
					.then((res) => {
						"deleted";
					})
					.then((err) => "error");
			}
			return "functionexit";
		});
	};

	calculateCashinHand = async () => {
		let www = localStorage.getItem("date");
		let date1 = new Date(www);
		date1.setDate(date1.getDate() - 1);
		let month = date1.getMonth();
		let date = date1.getDate();
		if (month < 10) {
			month = parseInt(month) + 1;
			month = "0" + month;
		}
		if (date < 10) {
			date = parseInt(date) + 1;
			date = "0" + date;
		}
		let prevDate = date1.getFullYear() + "-" + month + "-" + date;
		let ammountinhand;
		await axios.get(`${ammountinhandServer}`).then((res) => {
			let ammountinhand1 = res.data.filter((amh) =>
				amh.date.includes(prevDate)
			);
			ammountinhand1.map((data) => {
				return (ammountinhand = data.ammount);
			});
		});
		if (ammountinhand === undefined) {
			await this.setState({
				cashInHand: 0,
			});
		} else {
			await this.setState({
				cashInHand: ammountinhand,
			});
		}
	};

	calculateInversment = async () => {
		let Inversemet = 0;
		await axios.get(`${inversmentServer}`).then((res) => {
			let Inversemet1 = res.data.filter((inverse) =>
				inverse.date.includes(localStorage.getItem("date"))
			);
			Inversemet1.map((data) => {
				return (Inversemet = data.ammount);
			});
		});
		await this.setState({
			Inversemet,
		});
	};

	other_ammount_in = async () => {
		let other_ammount_in = 0;
		await axios.get(`${otherammuntINServer}`).then((res) => {
			let other_ammount_in1 = res.data.filter((other_ammount_in) =>
				other_ammount_in.date.includes(localStorage.getItem("date"))
			);
			other_ammount_in1.map((data) => {
				return (other_ammount_in =
					parseInt(data.ammount) + parseInt(other_ammount_in));
			});
		});
		await this.setState({
			other_ammount_in,
		});
	};

	closeUp = async () => {
		let closeUp = 0;
		await axios.get(`${closeupserver}`).then((res) => {
			let closeUp1 = res.data.filter((inverse) =>
				inverse.date.includes(localStorage.getItem("date"))
			);
			closeUp1.map((data) => {
				return (closeUp = parseInt(data.ammount) + parseInt(closeUp));
			});
		});
		await this.setState({
			closeUp,
		});
	};

	closeDown = async () => {
		let closeDown = 0;
		await axios.get(`${closeDownserver}`).then((res) => {
			let closeDown1 = res.data.filter((closeDown) =>
				closeDown.date.includes(localStorage.getItem("date"))
			);
			closeDown1.map((data) => {
				return (closeDown =
					parseInt(data.ammount) + parseInt(closeDown));
			});
		});
		await this.setState({
			closeDown,
		});
	};

	Others = async () => {
		let Others = 0;
		await axios.get(`${othersServer}`).then((res) => {
			let Others1 = res.data.filter((Others) =>
				Others.date.includes(localStorage.getItem("date"))
			);
			Others1.map((data) => {
				return (Others = parseInt(data.ammount) + parseInt(Others));
			});
		});
		await this.setState({
			Others,
		});
	};

	saveData = async () => {
		let { cashINHand1 } = this.state;
		let data = {
			date: localStorage.getItem("date"),
			ammount: cashINHand1,
		};
		await axios.post(`${postammountinhandServer}`, data).then((res) => {
			alert(`Data Saved To database Ammount In Hand: ${cashINHand1}`);
		});
		await this.getCostumerData();
		await localStorage.setItem("aih", cashINHand1);
	};

	render() {
		return (
			<>
				{/* <div>
					<p>இருப்பு : {this.state.cashInHand}</p>
					<p>DL வரவு: {this.state.dl}</p>
					<p>வட்டி வரவு: {this.state.intrest}</p>
					<p>MS வரவு: {this.state.ms}</p>
					<p>அட்டை அதிகம்: {this.state.closeUp}</p>
				</div> */}
				<div className="plancalc__">
					<h2>Cash In Hand: {this.state.cashINHand1}</h2>
					<button onClick={this.saveData}>Upload Today's Data</button>
				</div>
			</>
		);
	}
}

export default Plancalc;
