import React, { Component } from "react";
import "./App.css";
import {
	BrowserRouter as Router,
	Switch,
	Route,
	NavLink,
} from "react-router-dom";
import { FullScreen, useFullScreenHandle } from "react-full-screen";
import viewCostumers from "./components/viewCostumers/viewCostumers";
import addCostumer from "./components/addCostumer/addCostumer";
import updateCostumer from "./components/updateCostumer/updateCostumer";
import collectionDetails from "./components/collectionDetails/collectionDetails";
import addCollection from "./components/addCollection/addCollection";
import Settings from "./components/Settings/Settings";
import reports from "./components/reports/reports";
import expences from "./components/expences/expences";
import Plancalc from "./components/plancalc/planclac";
import Costumercollection from "./components/costumercollection/Costumercollection";
import Login from "./components/login/login";
import CollectionEntryComponent1 from "./components/collectionEntry/collectionEntry";

class App extends Component {
	constructor(props) {
		super(props);
		this.state = {
			company: "",
			loggedIn: false,
		};
	}

	componentDidMount() {
		const company = localStorage.getItem("company");
		const loggedIn = sessionStorage.getItem("access_token");
		if (company === null || loggedIn === null || loggedIn === undefined) {
			this.setState({
				company: "ABIPRAVI",
				loggedIn: false,
			});
		} else {
			this.setState({
				company,
				loggedIn: true,
			});
		}
		document.addEventListener("contextmenu", (e) => {
			e.preventDefault();
		});
	}

	render() {
		const navBarlist = [
			{ name: "View Costumer Data", path: "/viewcostumers" },
			{ name: "Add Costumer", path: "/addCostumer" },
			{ name: "Collection Details", path: "/collectionDetails" },
			{ name: "Add Collection", path: "/addCollection" },
			{ name: "Reports", path: "/reports" },
			{ name: "Upload Data", path: "/planclac" },
			{ name: "Settings", path: "/Settings" },
			{ name: "+", path: "/fastcollection" },
		];

		const route = [
			{ path: "/viewcostumers", component: viewCostumers },
			{ path: "/addCostumer", component: addCostumer },
			{ path: "/updateCostumer", component: updateCostumer },
			{ path: "/collectionDetails", component: collectionDetails },
			{ path: "/addCollection", component: addCollection },
			{ path: "/reports", component: reports },
			{ path: "/Settings", component: Settings },
			{ path: "/addExpence", component: expences },
			{ path: "/planclac", component: Plancalc },
			{ path: "/costumercollection", component: Costumercollection },
			{ path: "/fastcollection", component: CollectionEntryComponent1 },
		];

		return (
			<div className="App">
				<Router>
					<div className="d-flex gap-3 p-2 align-middle bg-dark justify-content-evenly">
						<p className="btn btn-seconday text-white fs-2">
							{this.state.company}
						</p>
						{this.state.loggedIn && (
							<div className="d-flex justify-content-evenly gap-3 p-2">
								{navBarlist.map((route) => {
									return (
										<NavLink
											key={route.name}
											exact
											className="btn btn-dark h-auto"
											activeClassName="btn"
											to={route.path}
										>
											{route.name}
										</NavLink>
									);
								})}
							</div>
						)}
					</div>
					<div className="App__mainContent">
						<Switch>
							{this.state.loggedIn === true &&
								route.map((router, index) => {
									return (
										<Route
											key={index}
											exact
											path={router.path}
											component={router.component}
										/>
									);
								})}
						</Switch>
						{this.state.loggedIn === false && <Login />}
					</div>
				</Router>
			</div>
		);
	}
}

export default App;
