import React, { useState, useEffect } from "react";
import Axios from "axios";

// Marteial Ui Imports
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
import EditRoundedIcon from "@material-ui/icons/EditRounded";
import DeleteForeverIcon from "@material-ui/icons/DeleteForever";
import Snackbar from "@material-ui/core/Snackbar";
import MuiAlert from "@material-ui/lab/Alert";
import Modal from "@material-ui/core/Modal";

import "./updateCostumer.css";

import { CSVLink } from "react-csv";

const serverURL = `getcostumer/`;
const updateCostumerURL = `updatecostumer`;
const deletecostumerServer = `deletecostumer`;

const AccessToken = sessionStorage.getItem("access_token");

function Alert(props) {
	return <MuiAlert elevation={6} variant="filled" {...props} />;
}

export default function UpdateCostumer() {
	const [costumerInput, setCostumerinput] = useState({
		name: "",
		loanammount: "",
		loan_date: "",
		loanInterst: "",
		ms: "",
		place: "",
		place_id: "",
	});
	const [costumerDetails, setCostumerDetails] = useState([
		{
			name: "",
			loanammount: "",
			loan_date: "",
			loanInterst: "",
			ms: "",
			place: "",
			place_id: "",
		},
	]);
	const [searchTerm, setSearchterm] = useState("");
	const [editCostumerId, setEditCostumerId] = useState(null);
	const [open, setOpen] = React.useState(false);
	const [modalopen, setModalOpen] = React.useState(false);

	const handleSubmit = (e) => {
		e.preventDefault();
		if (editCostumerId != null) {
			updateCostumer(costumerInput);
		}
	};

	const clearinputs = () => {
		setCostumerinput({
			name: "",
			loanammount: "",
			ammount_Balance: "",
			loan_date: "",
			loanInterst: "",
			ms: "",
			place: "",
			place_id: "",
		});
		setSearchterm("");
	};

	const updateCostumer = async (costumerdata) => {
		const url = `${updateCostumerURL}/${editCostumerId}/`;
		Axios.post(url, costumerdata).then(
			(result) => {
				setEditCostumerId(null);
				getcostumers();
				setModalOpen(false);
				clearinputs();
			},
			(error) => {
				console.log(error, "error");
			}
		);
	};

	const getcostumers = () => {
		const URL = `${serverURL}${AccessToken}/`;
		Axios.get(URL).then(
			(result) => {
				setCostumerDetails(result.data);
			},
			(error) => {
				alert("Error Loading Data");
			}
		);
		console.log(costumerDetails);
	};

	const handlechange = (e) => {
		setCostumerinput({
			...costumerInput,
			[e.target.name]: e.target.value,
		});
	};

	const editcostumerdata = (costumerData) => {
		const {
			id,
			name,
			loanammount,
			ammount_Balance,
			loan_date,
			loanInterst,
			ms,
			place,
			place_id,
		} = costumerData;
		setEditCostumerId(id);
		setCostumerinput({
			name,
			loanammount,
			ammount_Balance,
			loan_date,
			loanInterst,
			ms,
			place,
			place_id,
		});
		setModalOpen(true);
	};

	const deletecostumer = async (costumerInfo) => {
		const url = `${deletecostumerServer}/${costumerInfo.id}/${AccessToken}/`;
		await Axios.delete(url).then(
			(result) => {
				setOpen(true);
				getcostumers();
				clearinputs();
			},
			(error) => {
				console.log(error, "error");
			}
		);
	};

	const searchCostumer = (e) => {
		setSearchterm(e.target.value);
	};

	useEffect(() => {
		getcostumers();
	}, []);

	const handleClose = () => {
		setOpen(false);
	};

	const headers = ["name", "balance", "date", "1", "2"];
	const data = costumerDetails.map((data) => [
		data.name,
		data.ammount_Balance,
		data.loan_date,
	]);
	let content = {
		data: data,
		headers: headers,
		filename: `${new Date().toDateString()}_finDataabipravi.csv`,
	};
	const searchdata = costumerDetails
		.filter((details) => details.name.includes(searchTerm))
		.map((data, index) => {
			return (
				<tr className="" key={index}>
					<td>{data.name}</td>
					<td>{data.place}</td>
					<td>{data.loanammount}</td>
					<td>{data.ammount_Balance}</td>
					<td>{data.loan_date}</td>
					<td>{data.loanInterst}</td>
					<td style={{ padding: "12" }}>{data.ms}</td>
					<td>
						<button
							onClick={() => editcostumerdata(data)}
							className="btn btn-outline-dark"
						>
							<EditRoundedIcon />
						</button>
					</td>
					<td>
						<button
							onClick={() => deletecostumer(data)}
							className="btn btn-outline-dark"
						>
							<DeleteForeverIcon />
						</button>
					</td>
				</tr>
			);
		});

	return (
		<div className="">
			<Modal
				open={modalopen}
				onClose={handleClose}
				style={{
					color: "black",
					height: "70%",
					display: "flex",
					alignItems: "center",
					justifyContent: "center",
					"margin-top": 70,
				}}
			>
				<div className="d-flex flex-column gap-3 bg-white p-4 rounded-3">
					<h4>Update Costumer</h4>
					<input
						type="text"
						onChange={handlechange}
						value={costumerInput.name}
						placeholder="Costumer Name"
						className="form-control"
						name="name"
					/>
					<input
						type="text"
						onChange={handlechange}
						value={costumerInput.loanammount}
						placeholder="Loan Ammount"
						className="form-control"
						name="loanammount"
					/>
					<input
						type="text"
						onChange={handlechange}
						value={costumerInput.ammount_Balance}
						placeholder="Ammount Balance"
						className="form-control"
						name="ammount_Balance"
					/>
					<TextField
						type="date"
						name="loan_date"
						InputLabelProps={{
							shrink: true,
						}}
						defaultValue={costumerInput.loan_date}
						className="form-control"
						onChange={handlechange}
					/>
					<input
						type="text"
						onChange={handlechange}
						value={costumerInput.loanInterst}
						placeholder="Loan Intrest"
						name="loanIntrest"
						className="form-control"
					/>
					<input
						type="text"
						onChange={handlechange}
						value={costumerInput.ms}
						placeholder="MS"
						name="ms"
						className="form-control"
					/>
					<input
						type="text"
						onChange={handlechange}
						value={costumerInput.place}
						placeholder="Place"
						name="place"
						className="form-control"
					/>
					<input
						type="text"
						onChange={handlechange}
						value={costumerInput.place_id}
						placeholder="Place Id"
						name="place_id"
						className="form-control"
					/>
					<Button
						variant="outlined"
						style={{ outline: "none" }}
						color="primary"
						onClick={handleSubmit}
					>
						Update Costumer
					</Button>
				</div>
			</Modal>
			<div className="d-flex flex-column gap-3 h-auto">
				<div className="d-flex flex-row gap-3">
					<input
						onChange={searchCostumer}
						className="form-control w-25"
						placeholder="Search Costumers"
						type="search"
					/>
					<CSVLink {...content} className="btn btn-primary">
						Download Costumer Data
					</CSVLink>
				</div>
				<div className="overflow-auto" style={{ height: "70vh" }}>
					<table className="table table-bordered table-hover table-striped border-dark">
						<thead className="table-dark">
							<tr>
								<td>Name</td>
								<td>Place</td>
								<td>Loan Ammount</td>
								<td>Ammount Balance</td>
								<td>Loan Date</td>
								<td>Loan Intrest</td>
								<td>MS</td>
								<td>Edit</td>
								<td>Delete</td>
							</tr>
						</thead>
						<tbody className="">{searchdata}</tbody>
					</table>
				</div>
			</div>

			<Snackbar open={open} autoHideDuration={6000}>
				<Alert onClose={handleClose} severity="success">
					Costumer Deleted Successfully
				</Alert>
			</Snackbar>
		</div>
	);
}
